<template>
    <div>
        <top-nav title="商品列表"></top-nav>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" v-model="searchForm" class="search-form"  :inline="true">
                    <el-form-item  label="商品名称" label-width="100">
                            <el-input v-model="searchForm.goods_name"></el-input>
                    </el-form-item>
                    <el-form-item  label="商品编号" label-width="100">
                        <el-input v-model="searchForm.goods_sn" ></el-input>
                    </el-form-item>
                    <el-form-item  label="商品分类" label-width="100">
                       <product-category :no-add="true" :p-cat-id="searchForm.p_cat_id" :cat-id="searchForm.cat_id" @selCat="selCat"></product-category>
                    </el-form-item>
                    <el-form-item  label="商品模型" label-width="100">
                        <select-type v-model="searchForm.type_id" :type-id="searchForm.type_id"></select-type>
                    </el-form-item>
                    <el-form-item  label="上/下架" label-width="100">
                        <el-select v-model="searchForm.is_online"   placeholder="请选择">
                            <el-option label="全部" :value="0"></el-option>
                            <el-option label="上架" :value="2"></el-option>
                            <el-option label="下架" :value="1"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="扩展参数">
                            <el-checkbox :true-label="1"  :false-label="0" v-model="searchForm.is_free_shipping">包邮</el-checkbox>
                            <el-checkbox :true-label="1"  :false-label="0" v-model="searchForm.is_plus_vip" >会员</el-checkbox>
                            <el-checkbox :true-label="1"  :false-label="0" v-model="searchForm.is_commission" >分佣</el-checkbox>
                            <el-checkbox :true-label="1"  :false-label="0" v-model="searchForm.is_hot" >热销</el-checkbox>
                            <el-checkbox :true-label="1"  :false-label="0" v-model="searchForm.is_new" >新品</el-checkbox>
                            <el-checkbox :true-label="1"  :false-label="0" v-model="searchForm.is_recommend" >推荐 | </el-checkbox>
                    </el-form-item>
                    <el-form-item label="添加日期">
                        <date-between  v-model="searchForm.date"></date-between>
                    </el-form-item>
                    <div style="padding-left:80px;">
                        <el-button icon="el-icon-search" @click="getList(1)" size="mini" type="primary">搜索</el-button>
                        <router-link to="/product/product/create">
                            <el-button size="mini" type="danger">新增商品</el-button>
                        </router-link>
                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" size="small">
                    <el-table-column prop="goods_id" label="编码/ID" width="180">
                        <template slot-scope="scope">
                            <p>系统ID：<span>{{scope.row.goods_id}}</span></p>
                            <p>商品编码：<span>{{scope.row.goods_sn}}</span></p>
                        </template>
                    </el-table-column>

                    <el-table-column label="商品名称" width="240">
                        <template slot-scope="scope">
                            <div class="ellipsis-02">
                               <b> {{scope.row.goods_name}}</b>
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column label="缩略图" width="80" height="80">
                        <template slot-scope="scope">
                            <img width="60" :src="scope.row.photo"/>
                        </template>
                    </el-table-column>

                    <el-table-column label="商品价格" width="140">
                        <template slot-scope="scope">
                            <p>市场价：<span style="color: red">￥{{scope.row.market_price}}</span></p>
                            <p>本店价：<span style="color: red">￥{{scope.row.shop_price}}</span></p>
                        </template>
                    </el-table-column>

                    <el-table-column label="模型/分类" width="200">
                        <template slot-scope="scope">
                            <p>模型：<span>{{scope.row.type_id}}</span></p>
                            <p>分类：<span class="ellipsis-01">{{scope.row.p_cat_id}}-->{{scope.row.cat_id}}</span></p>
                        </template>
                    </el-table-column>

                    <el-table-column label="扩展信息" width="150">
                        <template slot-scope="scope">
                            <el-row>
                                <el-col :span="8">
                                    <el-tag v-if="scope.row.is_free_shipping == 1" size="mini">包邮</el-tag>
                                    <el-tag v-if="scope.row.is_free_shipping == 0" type="info" size="mini">包邮</el-tag>
                                </el-col>
                                <el-col :span="8">
                                    <el-tag v-if="scope.row.is_plus_vip == 1" size="mini">会员</el-tag>
                                    <el-tag v-if="scope.row.is_plus_vip == 0" type="info" size="mini">会员</el-tag>
                                </el-col>

                                <el-col :span="8">
                                    <el-tag v-if="scope.row.is_commission == 1" size="mini">分佣</el-tag>
                                    <el-tag v-if="scope.row.is_commission == 0" type="info" size="mini">分佣</el-tag>
                                </el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="8">
                                    <el-tag v-if="scope.row.is_hot == 1" size="mini">热销</el-tag>
                                    <el-tag v-if="scope.row.is_hot == 0" type="info" size="mini">热销</el-tag>
                                </el-col>
                                <el-col :span="8">
                                    <el-tag v-if="scope.row.is_new == 1" size="mini">新品</el-tag>
                                    <el-tag v-if="scope.row.is_new == 0" type="info" size="mini">新品</el-tag>
                                </el-col>
                                <el-col :span="8">
                                    <el-tag v-if="scope.row.is_recommend == 1" size="mini">推荐</el-tag>
                                    <el-tag v-if="scope.row.is_recommend == 0" type="info" size="mini">推荐</el-tag>
                                </el-col>
                            </el-row>
                        </template>
                    </el-table-column>

                    <el-table-column prop="goods_sn" label="浏/评/销/排">
                        <template slot-scope="scope">
                            <el-row>
                                <el-col :span="12"><p>浏览：<span>{{scope.row.views}}</span></p></el-col>
                                <el-col :span="12"><p>排序：<span>{{scope.row.orderby}}</span></p></el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="12"><p>评论：<span>{{scope.row.comment_count}}</span></p></el-col>
                                <el-col :span="12"><p>销量：<span>{{scope.row.sales_sum}}</span></p></el-col>
                            </el-row>
                        </template>
                    </el-table-column>

                    <el-table-column label="操作日期" >
                        <template slot-scope="scope">
                            <p>添加：{{scope.row.add_time}}</p>
                            <p>修改：{{scope.row.last_update}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column prop="goods_sn"  label="上/下架" width="80">
                        <template slot-scope="scope">
                            <el-switch :value="scope.row.is_online" :inactive-value="0" :active-value="1" @change="setOnline(scope.row)"></el-switch>
                        </template>
                    </el-table-column>

                    <el-table-column width="200" label="操作">
                        <template slot-scope="scope">
                            <el-row>
                                <el-col :span="8">
                                    <router-link :to="'/product/product/edit/' + scope.row.goods_id">
                                        <el-button size="mini"  type="text">编辑</el-button>
                                    </router-link>
                                </el-col>
                                <el-col :span="8">
                                    <router-link :to="'/product/product/specprice/' + scope.row.goods_id">
                                        <el-button size="mini" type="text">库存价格</el-button>
                                    </router-link>
                                </el-col>
                                <el-col :span="8" style="text-align: center">
                                    <el-popover placement="top" v-model="scope.row.is_delete">
                                        <p style="margin-bottom: 4px">确定删除吗？</p>
                                        <div style="text-align: right; margin: 0">
                                            <el-button :loading="loading" size="mini" type="text"
                                                       @click="scope.row.is_delete = false">取消
                                            </el-button>
                                            <el-button :loading="loading" type="primary" size="mini"
                                                       @click="del(scope)">确定
                                            </el-button>
                                        </div>
                                        <el-button type="text" size="mini" slot="reference" >删除</el-button>
                                    </el-popover>
                                </el-col>
                            </el-row>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="page">
                    <div class="page-tool"></div>
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>


<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import DateBetween from "../../../components/DateBetween";
    import ProductCategory from "../components/ProductCategory";
    import SelectType from "../type/components/SelectType";
    export default {
        name: 'goodsAttrIndex',
        components: {SelectType, ProductCategory, DateBetween, TopNav},
        data() {
            return {
                //整体菊花
                loading: false,
                //列表数据
                datas: [],
                //搜索数据
                searchForm: this.initSearch(),
                //批量操作
                goods_ids: [],
                goods_id: 0,
            }
        },
        created() {
            this.getList();
        },
        methods: {

            initSearch(){
                return {
                    date:[],
                    is_plus_vip:0,
                    is_commission:0,
                    is_free_shipping:0,
                    is_recommend:0,
                    is_new:0,
                    is_hot:0,
                    cat_id:0,
                    p_cat_id:0,
                    type_id:0,
                    is_online:0,
                    goods_name:'',
                    goods_sn:'',
                }
            },

            getList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.goods.goods/index", {
                    appToken: this.$store.getters.appToken,
                    page: page,
                    is_plus_vip:this.searchForm.is_plus_vip,
                    is_commission:this.searchForm.is_commission,
                    is_free_shipping:this.searchForm.is_free_shipping,
                    is_recommend:this.searchForm.is_recommend,
                    is_new:this.searchForm.is_new,
                    is_hot:this.searchForm.is_hot,
                    cat_id:this.searchForm.cat_id,
                    p_cat_id:this.searchForm.p_cat_id,
                    type_id:this.searchForm.type_id,
                    is_online:this.searchForm.is_online,
                    end_add_time:this.searchForm.date[1] ? this.searchForm.date[1] :  0,
                    bg_add_time:this.searchForm.date[0] ? this.searchForm.date[0] : 0,
                    goods_name:this.searchForm.goods_name,
                    goods_sn:this.searchForm.goods_sn,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            selCat(val){
                this.searchForm.cat_id = val.cat_id;
                this.searchForm.p_cat_id = val.p_cat_id;
            },


            setOnline(row) {
                this.loading = true;
                api.post("/shop/admin.goods.goods/online", {
                    appToken: this.$store.getters.appToken,
                    goods_id: row.goods_id,
                }).then(res => {
                    this.loading = false;
                    this.getList();
                }).catch((error) => {
                    this.loading = false;
                });
            },


            del: function (scope='') {
                if (scope != '' && scope.row.goods_id > 0) {
                    this.goods_id = scope.row.goods_id;
                } else {
                    if (this.goods_ids.length == 0) {
                        return false;
                    }
                }
                this.loading = true;
                api.post("/shop/admin.goods.goods/del", {
                    appToken: this.$store.getters.appToken,
                    //goods_ids: JSON.stringify(this.goods_ids),
                    goods_id: this.goods_id,
                }).then(res => {
                    this.getList();
                }).catch((error) => {
                    this.loading = false;
                });

            }
        }
    }
</script>

