<template>
    <div>
        <el-select style="width: 100%" v-loading="typeLoading"  :disabled="disabled === true"   @input="setTypeId" :size="size" :value="typeId" placeholder="请选择">
            <el-option
                    v-for="item in types"
                    :key="item.type_id"
                    :label="item.name"
                    :value="item.type_id">
            </el-option>
        </el-select>
    </div>
</template>

<script>
    import * as api from "@/api";
    export default {
        name: "SelectType",
        props:['size','typeId','disabled'],
        data(){
            return{
                typeLoading:true,
                types:[],
            }
        },
        created(){
            this.getTypeList();
        },
        methods:{
            setTypeId(val){
                this.$emit('input',val);
            },
            getTypeList(){
                this.typeLoading =true;
                api.post("/shop/admin.goodstype/index", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.typeLoading =false;
                    this.types = res.list;
                    this.types.unshift({
                        type_id:0,
                        name:"全部模型"
                    });
                }).catch((error) => {
                    this.typeLoading =false;
                });
            },
        },

    }
</script>

